////////////////////////////////////////
///////  MOBILE MENU NAVIGATION  ///////
var menu_button = document.getElementById('menu_button');
var menu_navigation = document.getElementById('menu_navigation');
menu_button.addEventListener('click', function(){
    menu_navigation.classList.toggle('show_menu');
});
window.addEventListener('keyup', function(e){
    if (e.keyCode === 27) {
        menu_navigation.classList.remove('show_menu');
    }
});

function hideMenu() {
    var menu_navigation = document.getElementById('menu_navigation');
    menu_navigation.classList.remove('show_menu');
}





/////////////////////////////////
///////  COUNTDOWN TIMER  ///////
var launch_date = new Date('09/03/2018 12:00:00');
var now = new Date();
var diff = launch_date - now;
if (diff > 0 ) {

    var span_secondes = document.getElementById('span_secondes');
    var span_minutes = document.getElementById('span_minutes');
    var span_heures = document.getElementById('span_heures');
    var span_jours = document.getElementById('span_jours');


    setInterval( function() {
        var now = new Date();
        var diff = launch_date - now;
        if (diff > 0 ) {
            var seconds = diff / 1000;
            var minutes = seconds / 60;
            var hours = minutes / 60;
            span_secondes.innerHTML = Math.floor(seconds) % 60;
            span_minutes.innerHTML = Math.floor(minutes) % 60;
            span_heures.innerHTML = Math.floor(hours) % 24;
            span_jours.innerHTML = Math.floor(hours / 24);
        };

    }, 1000 );

};









///////////////////////////////////
///////   SCROLL DOWN PAGE  ///////
function scrollIt(destination, duration = 200, easing = 'linear', callback) {

    const easings = {
        linear(t) {
            return t;
        },
        easeInQuad(t) {
            return t * t;
        },
        easeOutQuad(t) {
            return t * (2 - t);
        },
        easeInOutQuad(t) {
            return t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t;
        },
        easeInCubic(t) {
            return t * t * t;
        },
        easeOutCubic(t) {
            return (--t) * t * t + 1;
        },
        easeInOutCubic(t) {
            return t < 0.5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1;
        },
        easeInQuart(t) {
            return t * t * t * t;
        },
        easeOutQuart(t) {
            return 1 - (--t) * t * t * t;
        },
        easeInOutQuart(t) {
            return t < 0.5 ? 8 * t * t * t * t : 1 - 8 * (--t) * t * t * t;
        },
        easeInQuint(t) {
            return t * t * t * t * t;
        },
        easeOutQuint(t) {
            return 1 + (--t) * t * t * t * t;
        },
        easeInOutQuint(t) {
            return t < 0.5 ? 16 * t * t * t * t * t : 1 + 16 * (--t) * t * t * t * t;
        }
    };

    const start = window.pageYOffset;
    const startTime = 'now' in window.performance ? performance.now() : new Date().getTime();

    const documentHeight = Math.max(document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight);
    const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.getElementsByTagName('body')[0].clientHeight;
    const destinationOffset = typeof destination === 'number' ? destination : destination.offsetTop;
    const destinationOffsetToScroll = Math.round(documentHeight - destinationOffset < windowHeight ? documentHeight - windowHeight : destinationOffset);

    if ('requestAnimationFrame' in window === false) {
        window.scroll(0, destinationOffsetToScroll);
        if (callback) {
            callback();
        }
        return;
    }

    function scroll() {
        const now = 'now' in window.performance ? performance.now() : new Date().getTime();
        const time = Math.min(1, ((now - startTime) / duration));
        const timeFunction = easings[easing](time);
        window.scroll(0, Math.ceil((timeFunction * (destinationOffsetToScroll - start)) + start));

        if (window.pageYOffset === destinationOffsetToScroll) {
            if (callback) {
                callback();
            }
            return;
        }

        requestAnimationFrame(scroll);
    }

    scroll();
};


var scrollLinks = document.getElementsByClassName('scroll_link');
for (var li = 0; li < scrollLinks.length; li++) {
    const link = scrollLinks[li];
    link.addEventListener('click', (e) => {
        const destination = e.target.hash;
        scrollIt(
            document.querySelector(  destination ),
            1000,
            'easeInOutCubic',
            () => hideMenu()
        );
    });
}



